
.copyright-box{
	text-align: center;
	background-color: #5e17eb;
	color: white;
	font-size: 1vw;
	padding: .3vw 0vw;
	font-family: 'Poppins',sans-serif;
	letter-spacing: .1vw;
}



/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){

	.copyright-box{
		font-size: 2.5vw;
		padding: .5vw 0vw;
	}

}


/*small*/
@media screen and (min-width:577px) and (max-width:767px){
	.copyright-box{
		font-size: 2.5vw;
		padding: .5vw 0vw;
	}
}


